import React, {useState} from 'react'
import { Link } from "react-router-dom";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faBars } from '@fortawesome/free-solid-svg-icons'
import './navigation.scss'

export default function Index() {
    const [navBarToggled, setNavBarToggled] = useState(true);

    const toggleNavbar = () => {
        // e.preventDefault();
        console.log('Toggle Value: ',navBarToggled)
        setNavBarToggled(!navBarToggled);
    }

    return (
        <div className={navBarToggled ? 'navbar' : 'navbar collapse'}>
            <div className="nav-brand"><h1>Speed Foods</h1></div>
            <div className="toggle-collapse">
                <div className="toggle-icon"><button onClick={() => toggleNavbar()}><FontAwesomeIcon icon={faBars} /></button></div>
            </div>
            <div className="nav-menu">
                <ul className="nav-items">
                    <li> <Link onClick={() => toggleNavbar()} to="/">Home</Link> </li>
                    <li> <Link onClick={() => toggleNavbar()} to="/Portfolio">Portfolio</Link> </li>
                    <li> <Link onClick={() => toggleNavbar()} to="/About">About</Link> </li>
                    <li> <Link onClick={() => toggleNavbar()} to="/Blog">Blog</Link> </li>
                    <li> <Link onClick={() => toggleNavbar()} to="/Books">Books</Link> </li>
                    <li> <Link onClick={() => toggleNavbar()} to="/Contact">Contact</Link> </li>
                </ul>
            </div>
            <div className="nav-blank-menu">
            <h1></h1>
            <h1></h1>
            <h1>a</h1>
            </div>
        </div>
    )
}
